.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-height: 50vh;
  padding: 20px;
  font-family: Arial, sans-serif;

  /* 3D Metallic Gradient Background */
  background: linear-gradient(145deg, #1f3b4d, #618ab3, #1f3b4d);
  background-size: 400% 400%;
  animation: metallicGradient 10s ease infinite;

  /* Optional: add slight inner glow / bevel */
  box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.1),
              inset 0 0 20px rgba(0, 0, 0, 0.2);
}

/* Animate gradient for metallic shine effect */
@keyframes metallicGradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

h1 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #ffffff;
  text-shadow: 1px 1px 2px #000, 0 0 10px #aaa; /* 3D text shadow effect */
}

