.calculator {
  background: linear-gradient(135deg, #846af7, #25065e); /* gradient background */
  border-radius: 12px;
  padding: 1rem; /* smaller padding to reduce overall height */
  box-shadow: 0 0 10px rgba(7, 0, 0, 0.938);
  max-width: 500px;
  margin: auto;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 0.5rem; /* reduce spacing between fields */
}

input,
select {
  padding: 0.5rem; /* reduce input height */
  font-size: 0.9rem;
}

button {
  padding: 0.5rem; /* reduce button height */
  font-size: 0.9rem;
  background-color: #636e64;
  color: #f3efef;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #f5f8f5;
}

.image {
  width: 100%;
  max-height: 180px; /* reduce image height */
  object-fit: contain;
  margin-bottom: 0.8rem;
}
